@import url(https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Archivo+Black&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #6a98a0; }

#root {
  position: relative;
  height: 100vh;
  height: 480px;
  width: 800px;
  margin: auto;
  background: black;
  box-shadow: 0px 1px 8px 1px rgba(0, 0, 0, 0.2);
  overflow: hidden; }
  @media (max-width: 900px) {
    #root {
      box-shadow: none; } }

.serve-number {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.45rem;
  text-align: center; }

.winner-modal {
  position: absolute;
  width: 100vw;
  height: 90vh;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.center-btns {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0 15px;
  padding-bottom: 15px;
  background: black; }
  .center-btns button {
    background: grey;
    border-radius: 6px;
    height: 50px; }
  .center-btns__serve-num {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr 1fr; }

.btn--increment {
  color: white;
  background: #547c83;
  display: grid;
  grid-template-rows: 25% 75%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .btn--increment:first-of-type {
    -webkit-animation: flyInLeft 0.7s forwards;
            animation: flyInLeft 0.7s forwards; }
  .btn--increment:last-of-type {
    -webkit-animation: flyInRight 0.7s forwards;
            animation: flyInRight 0.7s forwards; }
  .btn--increment p {
    margin: 0;
    font-family: "Archivo Black", sans-serif;
    font-size: 3rem; }
  .btn--increment .team-name {
    padding-top: 32px;
    text-align: center; }
  .btn--increment .score {
    height: 75%;
    font-size: 14rem; }

.score {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem; }

.new-match-form {
  background: white;
  padding-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 150px;
  grid-gap: 30px; }
  .new-match-form h1 {
    grid-column: span 2; }
  .new-match-form label {
    padding: 0 20px; }
    .new-match-form label > * {
      margin-left: 10px; }

button.btn--full-width {
  width: 100%;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  margin: 0;
  background: #3aafa9;
  grid-column: span 2;
  color: white;
  font-size: 100px;
  font-weight: bold; }
  button.btn--full-width:focus {
    outline: none;
    background: #2d8984; }
  button.btn--full-width:active {
    background: #2d8984; }

.winner-screen {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column; }
  .winner-screen p {
    margin: auto; }

.keyboard__container {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: white; }
  .keyboard__container .hg-button-space {
    width: 75%; }
  .keyboard__container .simple-keyboard.hg-theme-default .hg-button {
    height: 80px;
    font-size: 1.9rem;
    font-weight: bold; }

@-webkit-keyframes flyInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0f, 0);
            transform: translate3d(0, 0f, 0);
    opacity: 1; } }

@keyframes flyInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0f, 0);
            transform: translate3d(0, 0f, 0);
    opacity: 1; } }

@-webkit-keyframes flyInRight {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0f, 0);
            transform: translate3d(0, 0f, 0);
    opacity: 1; } }

@keyframes flyInRight {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0); }
  to {
    -webkit-transform: translate3d(0, 0f, 0);
            transform: translate3d(0, 0f, 0);
    opacity: 1; } }

